:local(.home) {
  position: relative; }

:local(.label) {
  color: #737f73;
  position: absolute;
  text-align: center;
  margin: 0;
  padding-top: 20px;
  right: 20px; }

:local(.boxBar) {
  position: absolute;
  left: 0px;
  top: 0px;
  margin: 0; }

:local(.box1) {
  width: 40px;
  height: 40px;
  background-color: #70818c; }

:local(.box2) {
  width: 40px;
  height: 40px;
  background-color: #989b9d; }

:local(.box3) {
  width: 40px;
  height: 40px;
  background-color: #3a4454; }

:local(.box4) {
  width: 40px;
  height: 40px;
  background-color: #dbe1cc; }

:local(.box5) {
  width: 40px;
  height: 40px;
  background-color: #737f73; }

:local(.left) {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  height: 100vh;
  border-right: 2px solid #9ba49b;
  width: 50vw; }



